<template>
    <div class="dialog-container">
        <div class="dialog-bg" :class="{'dialog-bg-pc' : isPc}"></div>
        <div class="dialog-box" :class="{'reset-service-dialog-box': isPc, 'reset-service-dialog-box-mobile': !isPc}" @click="errorMessage = ''">
            <div class="first-row">
                <span>{{resetSuccess ? $t('RestServiceDialog.Reset-Success'): isResetAccount ? $t('RestServiceDialog.Reset-Account') : $t('RestServiceDialog.Reset-Service')}}</span>
                <i @click="closeDialog()"></i>
            </div>
            <div class="loading" v-if="loading">
                <a-spin>
                    <a-icon slot="indicator" type="loading" :style="{'font-size': 80 + 'px'}" spin/>
                </a-spin>
            </div>
            <div  class="second-row" v-if="!resetSuccess && !loading">
                <span class="tips-one">{{isResetAccount ? $t('RestServiceDialog.Address-Will-Change') : $t('RestServiceDialog.Reload-Address')}}</span>
                <span class="tips-two">{{$t('RestServiceDialog.Keep-Info')}}</span>
                <div class="url-box-bg">
                    <div class="left-box">
                        <div class="item-box">
                            <span class="left-span">{{$t('RestServiceDialog.Manage-Address')}}</span>
                            <span class="right-span">{{isResetAccount ? auAddress : url}}</span>
                        </div>
                        <div class="item-box">
                            <span class="left-span">{{$t('RestServiceDialog.Reset-Manage-Address')}}</span>
                            <span class="right-span">{{isResetAccount ? userAddress: newUrl}}</span>
                        </div>
                    </div>
                    <a-tooltip placement="top" trigger="click" :mouseLeaveDelay=0.5 :destroyTooltipOnHide=true>
                        <template slot="title">
                            <span>{{$t('RestServiceDialog.Copied')}}</span>
                        </template>
                        <i class="i-copy" @click="copyEvent()"></i>
                    </a-tooltip>
                </div>
                <span class="red-tips-span">{{$t('RestServiceDialog.If-Reset-Fail')}}</span>
                <span class="blue-tips-span" v-if="((bindEmailOrNot && emailBindStatus !== 'Binded') || auEmailStatus === 0) && isPc">{{$t('RestServiceDialog.Find-Back-Service')}}<span class="bind-email" @click="bindEmail">{{$t('RestServiceDialog.Bound-Email')}}</span></span>
                <span class="blue-tips-span" v-if="((bindEmailOrNot && emailBindStatus !== 'Binded') || auEmailStatus === 0) && !isPc">{{$t('RestServiceDialog.Find-Back-Service2')}}</span>
                <span class="bind-email" v-if="((bindEmailOrNot && emailBindStatus !== 'Binded') || auEmailStatus === 0) && !isPc" @click="bindEmail">{{$t('RestServiceDialog.Bound-Email2')}}</span>
                <span class="blue-tips-span" v-if="(bindEmailOrNot && emailBindStatus === 'Binded') || auEmailStatus !== 0">{{$t('RestServiceDialog.Already-Bound-Email')}}</span>
                <div>
                    <a-button class="btn-confirm" @click="resetService" :loading="startSubmit">{{$t('RestServiceDialog.Reset-Success2')}}</a-button>
                </div>
            </div>
            <div class="reset-success-box" v-if="resetSuccess && !loading">
                <span class="tips-one">{{$t('RestServiceDialog.Reset-Keep-Secret')}}</span>
                <div class="url-box-bg">
                    <div class="left-box">
                        <div class="left-url-box">
                            <span class="left-span">{{$t('RestServiceDialog.New')}}{{isResetAccount ? $t('RestServiceDialog.Account') : $t('RestServiceDialog.Service')}}{{$t('RestServiceDialog.Address')}}</span>
                            <span class="right-span">{{newUrl}}</span>
                        </div>
                    </div>
                    <a-tooltip placement="top" trigger="click" :mouseLeaveDelay=0.5 :destroyTooltipOnHide=true>
                        <template slot="title">
                            <span>{{$t('RestServiceDialog.Copied2')}}</span>
                        </template>
                        <i class="i-copy" @click="copyEvent()"></i>
                    </a-tooltip>
                </div>
                <div>
                    <a-button class="btn-confirm btn-ok" @click="closeDialog">{{$t('RestServiceDialog.Ok')}}</a-button>
                </div>
            </div>
            <div class="error-message">
                <span style="position: absolute">{{errorMessage}}</span>
            </div>
        </div>
    </div>
</template>
<script>
    import {AU_EMAIL_STATUS, AU_HMAC, AU_UUID} from "@/requestServer";

    export default {
        name: "AccessPointDialog",
        data(){
          return {
              auAddress: `${window.location.origin}?uuid=${localStorage.getItem(AU_UUID)}&hmac=${localStorage.getItem(AU_HMAC)}`,
              auEmailStatus: parseInt(localStorage.getItem(AU_EMAIL_STATUS)),
              permissionType: 0,
              errorMessage: '',
              item: {},
              resetSuccess: false,
              startSubmit: false,
              bindEmailOrNot: false,
              emailBindStatus:'',
              url: '',
              newUrl: '',
              copyContent: '',
              userName: '',
              uuid: '',
              isResetAccount: false,
              userAddress: '',
              newUuid: '',
              newHmac: '',
              isPc : this.$isPc(),
              loading: true,
          }
        },
        props: {
            currentUrl: {},
            emailStatus: {},
            allowBindEmail:{},
            resetAccount:{},
        },
        watch: {
            resetAccount: {
                immediate: true,
                handler(value) {
                    if (value !== undefined) {
                        this.isResetAccount = value;
                        this.preResetService();
                    }
                }
            },
            currentUrl: {
                immediate: true,
                handler(value) {
                    if (value !== undefined) {
                        this.url = value;
                        this.preResetService();
                    }
                }
            },
            emailStatus: {
                immediate: true,
                handler(value) {
                    if (value !== undefined) {
                        this.emailBindStatus = value;
                    }
                }
            },
            allowBindEmail: {
                immediate: true,
                handler(value) {
                    if (value !== undefined) {
                        this.bindEmailOrNot = value;
                    }
                }
            },
        },
        methods:{
            copyEvent() {
                var textarea = document.createElement("textarea");
                textarea.value = this.copyContent;
                textarea.readOnly = true;
                document.body.appendChild(textarea);
                textarea.select(); // 选择对象
                document.execCommand("Copy"); // 执行浏览器复制命令
                textarea.className = "textarea";
                textarea.style.display = "none";
            },
            preResetService(){
                const that = this;
                if (this.isResetAccount) {
                    let param = {
                        "uuid": localStorage.getItem(AU_UUID),
                        "hmac": localStorage.getItem(AU_HMAC),
                    };
                    this.$request.auRestPreview(param,function (data) {
                        if (data && (data.status === 200 || data.status === 201)) {
                            that.loading = false;
                            that.newUuid = data.data.uuid;
                            that.newHmac = data.data.hmac;
                            that.userAddress = data.data.userAddress;
                            that.copyContent = that.$t('Now-Manage-Address') + that.auAddress + '\n' + that.$t('Reset-Manage-Address') + that.userAddress;
                        }
                    })
                } else {
                    this.$request.preResetService(function (data) {
                        if ((data.status === 200 || data.status === 201) && data.data) {
                            that.loading = false;
                            that.userName = data.data.username;
                            that.uuid = data.data.uuid;
                            that.newUrl = data.data.dashboardUrl;
                            that.copyContent = that.$t('Now-Manage-Address') + that.url + '\n' + that.$t('Reset-Manage-Address') + that.newUrl;
                        } else {
                            if (data && data.message) {
                                that.errorMessage = data.message;
                            } else {
                                that.errorMessage = 'error';
                            }
                        }
                    });
                }
            },
            resetService() {
                this.startSubmit = true;
                const that = this;
                if (this.isResetAccount) {
                    let param = {
                        "uuid": localStorage.getItem(AU_UUID),
                        "hmac": localStorage.getItem(AU_HMAC),
                        "newUuid": this.newUuid,
                        "newHmac": this.newHmac,
                    };
                    this.$request.auResetConfirm(param,function (data) {
                        that.startSubmit = false;
                        if ((data.status === 200 || data.status === 201) && data.data) {
                            that.resetSuccess = true;
                            localStorage.setItem(AU_UUID, data.data.uuid);
                            localStorage.setItem(AU_HMAC, data.data.hmac);
                            that.$emit('closeDialog',that.resetSuccess);
                        } else {
                            if (data && data.message) {
                                that.errorMessage = data.message;
                            } else {
                                that.errorMessage = 'error';
                            }
                        }
                    });
                } else {
                    this.$request.resetService(this.userName,this.uuid,function (data) {
                        that.startSubmit = false;
                        if (data.status === 200 && data.data) {
                            that.resetSuccess = true;
                            that.newUrl = data.data.dashboardUrl;
                            that.copyContent = '新服务地址: ' + that.newUrl;
                        } else {
                            if (data && data.message) {
                                that.errorMessage = data.message;
                            } else {
                                that.errorMessage = 'error';
                            }
                        }
                    });
                }
            },
            bindEmail() {
                this.$emit('bindEmail',true);
            },
            closeDialog(){
                this.errorMessage = '';
                this.$emit('closeDialog',this.resetSuccess,this.newUrl)
            }
        }
    }
</script>

